<template>
  <div class="movie-detail">
    <div class="movie-detail-item" v-if="LOADING">
      <vcl-facebook></vcl-facebook>
    </div>
    <div class="movie-detail-item" v-if="!LOADING">
      <div class="image">
        <img :src="movie.poster_path | setImagePath" />
      </div>
      <div class="content">
        <div class="head">
          <div>{{movie.original_title}}</div>
          <div>{{movie.tagline}}</div>
        </div>
        <p class="title">THE RATE</p>
        <div class="rate">
          <rate :length="10" :value="movie.vote_average" :readonly="true" />
        </div>
        <p class="title">THE INFO</p>
        <div
          class="small-info"
        >{{ movie.spoken_languages.length > 0 ? movie.spoken_languages[0].name + ' /' : ''}} {{movie.runtime}} MIN / {{movie.release_date}}</div>
        <p class="title">THE GENRES</p>
        <div class="genres-info">
          <p v-for="item in movie.genres" :key="item.id">
            <router-link :to="{ name: 'genres', params: { genres: item.name }}" :key="item.id">
              <span></span>
              {{item.name}}
            </router-link>
          </p>
        </div>
        <p class="title">THE description</p>
        <div class="desc">
          <p>{{movie.overview}}</p>
        </div>
        <p class="title">THE LINK</p>
        <div class="link">
          <ul>
            <li>
              <router-link :to="imdbLink(movie.imdb_id)" target="_blank">
                <img src="https://icon-library.net/images/imdb-icon/imdb-icon-28.jpg" alt />
              </router-link>
            </li>
            <li>
              <a :href="ownLink(movie.homepage)" target="_blank">
                <img src="https://image.flaticon.com/icons/png/512/12/12195.png" alt />
              </a>
            </li>
            <li>
              <img
                src="https://image.flaticon.com/icons/png/512/16/16030.png"
                alt
                @click="show(movie)"
              />
            </li>
          </ul>
        </div>
      </div>
    </div>
    <modal name="hello-world" width="60%" height="60%">
      <youtube :video-id="video" />
    </modal>
  </div>
</template>


<script>
import { mapState, mapGetters } from "vuex";
import { VclFacebook } from "vue-content-loading";

export default {
  data() {
    return {
      video: ""
    };
  },
  head: {
    title: {
      inner: "Movie Detail"
    }
  },
  computed: {
    ...mapState({
      movie: state => state.movie.singleMovie
    }),
    ...mapGetters({
      LOADING: "movie/getMovieLoader"
    })
  },
  mounted() {
    this.initMovieDetail();
  },
  methods: {
    initMovieDetail() {
      let id = this.$route.params.id;
      this.$store.dispatch("movie/getDetail", { id });
    },
    imdbLink(value) {
      if (!value) return "/";
      return `//www.imdb.com/title/` + value;
    },
    ownLink(value) {
      if (!value) return "/";
      return value;
    },
    show(value) {
      this.video = value.videos.results[0]["key"];
      this.$modal.show("hello-world");
    },
    hide() {
      this.$modal.hide("hello-world");
    }
  },
  watch: {
    $route() {
      this.initMovieDetail();
    }
  },
  components: { VclFacebook }
};
</script>


